@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --gradient-1: linear-gradient(135deg, #ffd3a5 10%, #fd6585 100%);
  --gradient-2: linear-gradient(315deg, var(--cyan) 0%, #e0c3fc 100%);
  --gradient-3: linear-gradient(210deg, #74ebd5 0%, #f7e8ad 100%);
  --gradient-4: linear-gradient(190deg, var(--pink) 0%, var(--orange) 100%);
  --gradient-5: linear-gradient(210deg, var(--red) 0%, var(--blue) 100%);
  --gradient-6: linear-gradient(135deg, var(--violet) 10%, var(--yellow) 100%);
}

html,
body,
.App,
#root {
  width: 100% !important;
  height: 100% !important;
  position: relative;
}

#navbar {
  position: fixed;
  width: 100%;
  height: 92px;
  top: 0px;
  z-index: 99999;
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 250ms ease-in;
}

.nav-link {
  font-size: calc(16px + 0.1vw);
}

.titillium-text {
  font-family: "Titillium Web", sans-serif !important;
}

.poppins-text {
  font-family: "Poppins", sans-serif;
}

.lead-text {
  font-size: calc(12px + 0.4vw);
  font-weight: 300;
}

.main-text {
  font-size: calc(36px + 3vw);
  font-weight: 300;
  line-height: 1.2;
  text-align: center;
}

.sub-text {
  font-size: calc(24px + 1vw);
  font-weight: 300;
  line-height: 1.2;
}

.badge-gutter {
  padding: 0px 10px 0px 0px;
  margin: 5px 0px;
  position: relative;
}

.age-text {
  background: linear-gradient(to right, lightgreen, cyan, lightblue, violet);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: monospace;
}

.position-rel {
  position: relative !important;
  display: flex;
  flex-flow: row;
}

.border-faint {
  border-bottom: 1px solid dimgrey !important;
}

#custom-card-column {
  padding: 0px 22px 0px 22px !important;
  max-height: 90%;
  width: 100%;
  overflow-y: overlay;
  overflow-x: hidden;
}

.custom-card-container {
  padding-top: 62px;
}

.link-css:hover {
  fill: var(--pink);
}

a {
  text-decoration: none !important;
}

.nav-hover-dark:hover {
  color: white !important;
}

.nav-hover-light:hover {
  color: black !important;
}

.selected-dark {
  color: white !important;
  font-weight: 600 !important;
}

.selected-light {
  color: black !important;
  font-weight: 600 !important;
}

.neu-card {
  height: 150px;
  width: 150px;
  border-radius: 25px;
  margin: 65px;
  background: #343a40;
  box-shadow: 20px 20px 45px #2c3136, -20px -20px 45px #3c434a;
}

.neu-card svg {
  height: 25%;
  width: 25%;
}

.container-holder {
  padding-top: 50px;
  max-height: 90%;
  width: 100%;
  overflow-y: visible;
  overflow-x: hidden;
}

.bracket {
  color: var(--red);
}

#custom-card-column-x {
  padding: 0px 22px 0px 22px !important;
  max-height: 90%;
  overflow-y: hidden;
  overflow-x: auto;
}

.flex-direction-row {
  display: flex;
  flex-direction: row;
  width: 100% !important;
  overflow-x: auto;
  padding-bottom: 15px;
  max-height: 80vh;
}

.skill-card {
  width: 35vh !important;
  height: 70vh !important;
  border-radius: 25px 25px !important;
  position: relative !important;
}

.image-center {
  background-repeat: no-repeat;
  background-size: auto;
  background-position: center;
}

.row-wrapper {
  width: 100% !important;
  display: flex;
  align-items: center !important;
  justify-content: space-between !important;
  flex-direction: row;
  overflow-x: auto;
}

